@font-face {
  font-family: "Cera Pro Bold";
  src: url("../../fonts/Roboto/Cera-pro/Cera\ Pro\ Bold.ttf");
}
@font-face {
  font-family: "Cera Pro Regular";
  src: url("../../fonts/Roboto/Cera-pro/Cera\ Pro\ Regular\ Italic.ttf");
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 1020px;
  /* border:1px solid #25D366; */
}

.wrapper {
  width: 1180px;
  height: 1025px;
  margin-top: 50px;
}

.grid {
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin: 0 auto;
  width: 1180px;
  height: 500px;
  /* border: 1px solid violet; */
}

.h2 {
  font-family: "Cera Pro Bold";
  font-size: 44px;
  line-height: 100%;
  color: white;
}

.wrapper_btn {
  padding-top: 40px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  gap: 30px;
}

.btn {
  font-family: "Cera Pro Regular";
  font-size: 18px;
  line-height: 27px;
  color: black;
  background-color: #c8a35f;
  color: black;
  width: 242px;
  height: 55px;
  border-radius: 10px;
  margin-top: 20px;
  border: none;
}

@media (max-width: 400px) {
  .main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    margin: 0 10px;
    width: 370px;
    height: 100%;
    /* border:1px solid #25D366; */
  }
  .wrapper {
    width: 370px;
    height: 100%;
    padding-bottom: 30px;
    margin-top: 0px;
  }

  .grid {
    padding: 0;
    margin: 0 auto;
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: 1fr;
    gap: 12px;
    margin: 0 auto;
    width: 370px;
    height: 100%;
    /* border: 1px solid violet; */
  }
  .h2 {
    font-family: "Cera Pro Bold";
    font-size: 34px;
    line-height: 100%;
    color: white;
  }
  .wrapper_btn {
    padding-top: 20px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  .btn {
    font-family: "Cera Pro Regular";
    font-size: 10px;
    line-height: 27px;
    color: black;
    background-color: #c8a35f;
    color: black;
    width: 155px;
    height: 45px;
    border-radius: 10px;
    margin-top: 20px;
    border: none;
  }
}
