@font-face {
  font-family: "Cera Pro Bold";
  src: url("../../fonts/Roboto/Cera-pro/Cera\ Pro\ Bold.ttf");
}

.grid_layout {
  display: grid;
  grid:
    "ava name" 1fr
    "ava quote" 4fr
    / 1.2fr 2fr;
  gap: 8px;

  width: 950px;
  min-width: 950px;
  height: 550px;
  /* border: 9px solid rgb(166, 214, 53); */
  background-color: #393f47;
  /* padding: 20px; */
}

.ava {
  grid-area: ava;
  border: 2px solid black;
  width: 300px;
  height: 400px;
  box-sizing: border-box;
  border-radius: 20px;
  object-fit: cover;
  margin: 30px;
}

.name {
  font-family: "Cera Pro Bold";
  font-size: 32px;
  margin-top: 30px;
  margin-bottom: 0;
  grid-area: name;
  /* border: 2px solid red; */
}

.quote {
  padding-top: 0;
  grid-area: quote;
  font-size: 24px;
  border-radius: 20px;
  color: white;
  /* padding: 20px; */
  /* border: 2px solid red; */
}
